import React, { useEffect, useState, useRef } from "react";
import "./Navbar.css";
import { NavLink } from "react-router-dom";
import { useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";
import { BsCart, BsPersonCircle, BsHeart, BsShop, BsList, BsX } from "react-icons/bs";
import logo from "../pictures/logo.png";
import { logoutUser } from "../utils/auth";
import fuzzy from "fuzzy";

export default function Navbar() {
  const Cart = useSelector((state) => state.reducer.cart);
  const Products = useSelector((state) => state.reducer.products);
  const navigate = useNavigate();

  // Dropdown state
  const [showDropdown, setShowDropdown] = useState(false);
  const [showMobileMenu, setShowMobileMenu] = useState(false);
  const dropdownRef = useRef();

  // Search state
  const [search, setSearch] = useState("");
  const [results, setResults] = useState([]);

  // Fuzzy search logic
  useEffect(() => {
    if (search.trim() && Products.length) {
      const options = {
        extract: (el) => el.title,
      };
      const matches = fuzzy.filter(search, Products, options);
      setResults(matches.map((match) => match.original));
    } else {
      setResults([]);
    }
  }, [search, Products]);

  // Close dropdown on outside click
  useEffect(() => {
    function handleClickOutside(event) {
      if (dropdownRef.current && !dropdownRef.current.contains(event.target)) {
        setShowDropdown(false);
      }
    }
    document.addEventListener("mousedown", handleClickOutside);
    return () => document.removeEventListener("mousedown", handleClickOutside);
  }, []);

  // Close mobile menu on window resize
  useEffect(() => {
    function handleResize() {
      if (window.innerWidth > 768) {
        setShowMobileMenu(false);
      }
    }
    window.addEventListener("resize", handleResize);
    return () => window.removeEventListener("resize", handleResize);
  }, []);

  const handleLogout = () => {
    logoutUser();
    navigate("/login");
    setShowDropdown(false);
    setShowMobileMenu(false);
  };

  const handleSearchSelect = (product) => {
    setSearch("");
    setResults([]);
    navigate(`/products/${product.id}`);
    setShowMobileMenu(false);
  };

  const handleSearchSubmit = (e) => {
    e.preventDefault();
    if (results.length > 0) {
      handleSearchSelect(results[0]);
    }
  };

  const handleNavClick = () => {
    setShowMobileMenu(false);
    setShowDropdown(false);
  };

  return (
    <div className="nav-bar">
      <NavLink to="/home" onClick={handleNavClick}>
        <div className="nav-logo">
          <img src={logo} alt="Flipmart Logo" />
          <p>Flipmart</p>
        </div>
      </NavLink>

      {/* Mobile menu toggle */}
      <button 
        className="mobile-menu-toggle"
        onClick={() => setShowMobileMenu(!showMobileMenu)}
        aria-label="Toggle mobile menu"
      >
        {showMobileMenu ? <BsX /> : <BsList />}
      </button>

      {/* Desktop Navigation Links */}
      <div className="nav-links">
        <div className="navlink-container">
          <NavLink
            to="/home"
            className={({ isActive }) => (isActive ? "activenav" : "")}
            onClick={handleNavClick}
          >
            Home
          </NavLink>
          <NavLink
            to="/products"
            className={({ isActive }) => (isActive ? "activenav" : "")}
            onClick={handleNavClick}
          >
            Products
          </NavLink>
        </div>
      </div>

      {/* Desktop Search bar */}
      <div className="nav-search-wrapper">
        <form
          className="nav-search-form"
          onSubmit={handleSearchSubmit}
          autoComplete="off"
        >
          <input
            type="text"
            className="nav-search-input"
            placeholder="Search products..."
            value={search}
            onChange={(e) => setSearch(e.target.value)}
          />
          {search && results.length > 0 && (
            <ul className="nav-search-results">
              {results.slice(0, 5).map((product) => (
                <li
                  key={product.id}
                  onClick={() => handleSearchSelect(product)}
                  className="nav-search-result-item"
                >
                  {product.title}
                </li>
              ))}
            </ul>
          )}
        </form>
      </div>

      {/* Cart and Profile actions */}
      <div className="nav-actions">
        <div className="nav-cart">
          <button onClick={() => { navigate("/cart"); handleNavClick(); }}>
            <span className="cart-icon">
              <BsCart />
            </span>
            <p>{Cart.length}</p>
          </button>
        </div>
        
        <div className="nav-profile" ref={dropdownRef}>
          <button
            className="profile-btn"
            onClick={() => setShowDropdown((prev) => !prev)}
            aria-label="Profile menu"
          >
            <BsPersonCircle size={24} />
          </button>
          {showDropdown && (
            <div className="profile-dropdown">
              <button
                onClick={() => {
                  setShowDropdown(false);
                  navigate("/wishlist");
                }}
              >
                <BsHeart /> Wishlist
              </button>
              <button
                onClick={() => {
                  setShowDropdown(false);
                  navigate("/orders");
                }}
              >
                Orders
              </button>
              <button
                onClick={() => {
                  setShowDropdown(false);
                  navigate("/seller");
                }}
              >
                <BsShop /> Seller Page
              </button>
              <button onClick={handleLogout}>
                Log Out
              </button>
            </div>
          )}
        </div>
      </div>

      {/* Mobile Menu */}
      <div className={`mobile-menu ${showMobileMenu ? 'open' : ''}`}>
        <div className="mobile-nav-links">
          <NavLink
            to="/home"
            className={({ isActive }) => (isActive ? "activenav" : "")}
            onClick={handleNavClick}
          >
            Home
          </NavLink>
          <NavLink
            to="/products"
            className={({ isActive }) => (isActive ? "activenav" : "")}
            onClick={handleNavClick}
          >
            Products
          </NavLink>
        </div>
        
        <div className="mobile-search">
          <form
            className="nav-search-form"
            onSubmit={handleSearchSubmit}
            autoComplete="off"
          >
            <input
              type="text"
              className="nav-search-input"
              placeholder="Search products..."
              value={search}
              onChange={(e) => setSearch(e.target.value)}
            />
            {search && results.length > 0 && (
              <ul className="nav-search-results">
                {results.slice(0, 5).map((product) => (
                  <li
                    key={product.id}
                    onClick={() => handleSearchSelect(product)}
                    className="nav-search-result-item"
                  >
                    {product.title}
                  </li>
                ))}
              </ul>
            )}
          </form>
        </div>
        
        <div className="mobile-actions">
          <button onClick={() => { navigate("/wishlist"); handleNavClick(); }}>
            <BsHeart /> Wishlist
          </button>
          <button onClick={() => { navigate("/orders"); handleNavClick(); }}>
            Orders
          </button>
          <button onClick={() => { navigate("/seller"); handleNavClick(); }}>
            <BsShop /> Seller
          </button>
          <button onClick={handleLogout}>
            Log Out
          </button>
        </div>
      </div>
    </div>
  );
}