import React from "react";
import { useSelector, useDispatch } from "react-redux";
import { useNavigate } from "react-router-dom";
import { useToast } from "./ToastContext";
import { removeFromWishlist } from "./Redux/Actions";
import "./Wishlist.css";

export default function Wishlist() {
  const wishlist = useSelector((state) => state.reducer.wishlist);
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const toast = useToast();

  const handleRemoveFromWishlist = (id) => {
    dispatch(removeFromWishlist(id));
    toast("Removed from Wishlist");
  };

  const handleViewDetails = (id) => {
    navigate(`/products/${id}`);
  };

  const handleShopNow = () => {
    navigate("/products");
  };

  if (wishlist.length === 0) {
    return (
      <div className="wishlist-container">
        <div className="empty-wishlist">
          <h2>Your Wishlist is Empty</h2>
          <p>Discover amazing products and add them to your wishlist!</p>
          <button onClick={handleShopNow}>
            Start Shopping
          </button>
        </div>
      </div>
    );
  }

  return (
    <div className="wishlist-container">
      <h2>Your Wishlist ({wishlist.length} items)</h2>
      <div className="wishlist-grid">
        {wishlist.map((item) => (
          <div className="wishlist-card" key={item.id}>
            <img src={item.image} alt={item.title} />
            <h3>{item.title}</h3>
            <div className="price">${item.price}</div>
            <div className="actions">
              <button onClick={() => handleViewDetails(item.id)}>
                View Details
              </button>
              <button 
                className="remove-btn"
                onClick={() => handleRemoveFromWishlist(item.id)}
              >
                Remove
              </button>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
}