export const TYPE = {
  getproducts: "GETPRODUCTS",
  getproductid: "GETPRODUCTID",
  addtocart: "ADDTOCART",
  increaseqty: "INCRESEQTY",
  decreaseqty: "DECREASEQTY",
  deleteitem: "DELETEITEM",
  filterData: "FILTERDATA",
  getAddress: "GETADDRESS",
  order: "Orders",
  reset: "RESET",
  addToWishlist: "ADD_TO_WISHLIST",
  loadCart: "LOAD_CART",
};

export const resetCart = () => {
  return {
    type: TYPE.reset,
  };
};

export const getOrders = (data) => {
  return {
    type: TYPE.order,
    payload: data,
  };
};

export const filteredProductData = (data) => {
  return {
    type: TYPE.filterData,
    payload: data,
  };
};

export const getProducts = (data) => {
  return {
    type: TYPE.getproducts,
    payload: data,
  };
};

export const getAddress = (address) => {
  return {
    type: TYPE.getAddress,
    payload: address,
  };
};

// Load cart from API
export const loadCart = () => async (dispatch) => {
  try {
    const response = await fetch('https://localhost:7051/api/Cart');
    if (response.ok) {
      const data = await response.json();
      dispatch({
        type: TYPE.loadCart,
        payload: data
      });
    } else {
      console.warn('Backend server responded with error status:', response.status);
      // Initialize with empty cart if backend is not available
      dispatch({
        type: TYPE.loadCart,
        payload: []
      });
    }
  } catch (error) {
    console.warn('Backend server not available, using local cart state:', error.message);
    // Initialize with empty cart if backend is not available
    dispatch({
      type: TYPE.loadCart,
      payload: []
    });
  }
};

// Add to cart with API call
export const addToCart = (item) => async (dispatch) => {
  try {
    const response = await fetch('https://localhost:7051/api/Cart/Add', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({
        id: item.id,
        title: item.title,
        price: item.price,
        image: item.image,
        description: item.description,
        rating: item.rating,
        qty: 1
      })
    });
    
    if (response.ok) {
      const data = await response.json();
      dispatch({
        type: TYPE.addtocart,
        payload: item
      });
    } else {
      console.warn('Backend server responded with error status:', response.status);
      // Fallback to local state if API fails
      dispatch({
        type: TYPE.addtocart,
        payload: item
      });
    }
  } catch (error) {
    console.warn('Backend server not available, using local cart state:', error.message);
    // Fallback to local state if API fails
    dispatch({
      type: TYPE.addtocart,
      payload: item
    });
  }
};

// Delete item from cart
export const DeleteItem = (id) => async (dispatch) => {
  try {
    const response = await fetch(`https://localhost:7051/api/Cart/${id}`, {
      method: 'DELETE'
    });
    
    if (response.ok) {
      dispatch({
        type: TYPE.deleteitem,
        payload: id
      });
    } else {
      console.warn('Backend server responded with error status:', response.status);
      // Fallback to local state if API fails
      dispatch({
        type: TYPE.deleteitem,
        payload: id
      });
    }
  } catch (error) {
    console.warn('Backend server not available, using local cart state:', error.message);
    // Fallback to local state if API fails
    dispatch({
      type: TYPE.deleteitem,
      payload: id
    });
  }
};

